  border-right: 1px solid white;
  top: 50%;
  transform: translateY(-50%);
  img {
    position: absolute;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    top: 0;
  }
}
.hamburger {
  position: fixed;
  right: 20px;
  top: 30px;
  img {
    width: 30px;
  }
}
</style>
